body {
  margin: 0;
  padding: 0;
}


* {
  box-sizing: border-box;
}


h1, h2, h3, h4, h5, h6 {
  margin: 0 0 1rem 0;
  color: #0B0C10;
}


body {
  font-family: "Open Sans",Helvetica,sans-serif;
  font-size: 15pt;
  font-weight: 400;
  background-color: #f0f0f1;
}


#header {
  background-color: #0B0C10;
  color: #fff5f5;
  font-size: 2rem;
  line-height: 3.5rem;
  padding: 0 1rem;
  letter-spacing: 0.225rem;

  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  nav, nav > ul {
    display: flex;
    flex-direction: row;
    width: 100%;
  }

  nav > ul > li {
    margin-right: 1rem;
  }
}


#splash {
  background: #1F2833 linear-gradient(#0B0C10, #1F2833, #0B0C10);
  color: #66FCF1;
  margin-bottom: 1rem;

  h2 {
    color: #66FCF1;
    letter-spacing: 0.1rem;
  }
}


#splash header {
  padding: 12rem 0 10rem 0;
  text-align: center;
}


#main {
  width: 60rem;
  margin: 0 auto;
}


#content {
  width: 60rem;
  margin: 0 auto;
}


.mr-auto {
  margin-right: auto !important;
}